social-container {
    background: #eee;
    padding: 25px 50px;
    align-self: center;
}

a.social {
    margin: 0 1rem;
    transition: transform 250ms;
    display: inline-block;
    
}

a.social:hover {
        transform: translateY(-2px);
}

a.facebook {
    color: #4968ad;
}

a.instagram {
    color: black;
}

a.github{
    color:orangered;
    
}